// Select 2
@import "select2";
@import '~flatpickr/dist/flatpickr.css';


.select2-container .select2-selection--single{
    border: 1px solid #cfdadd;
    border-radius: 2px;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    color: #555;
    height:37px;
    padding: 5px;
}

.msgAlert{
    min-height: 100px;
    position: absolute;
    top: 50px;
    right: 20px;
    width: 300px;
    z-index: 100;
}

ul.checktree ul{
    padding-inline-start: 40px !important;
    list-style: none;
}
ul.checktree li:before {
    height: 1em;
    width: 12px;
    border-bottom: 1px dashed;
    content: "";
    display: inline-block;
    top: -0.3em;
}

ul.checktree li { border-left: 1px dashed; padding-bottom: 5px }

ul.checktree li:last-child:before { border-left: 1px dashed; }

ul.checktree li:last-child { border-left: none; }
ul.checktree label{
    margin-left: 10px;
}
ul.checktree{
    list-style: none;
    padding: 0;
}

.c,.c a{
    background: #BFF0DE !important;
    color: darkgreen;
}

.d,.d a{
    background: #f8ccc8 !important;
    color: red;
    font-weight: bold;
}

.c .btn,.d .btn{
    color: #fff !important;
}
.c .btn-primary,.d .btn-primary{
    background-color: #4e73df !important;
}
.c .btn-danger,.d .btn-danger{
    background-color: #e02d1b !important;
}
